body {
  background-image: linear-gradient(to right, #be3449, #e0904f);
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.search-form {
  z-index: 10;
  height: 40px;
  background: #2f3640;
  border-radius: 40px;
  padding: 10px;
  position: fixed;
  top: 0%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.searchInput {
  float: left;
  width: 0;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  font-size: 16px;
  line-height: 40px;
  transition: all .4s;
}

.searchButton {
  float: right;
  width: 40px;
  height: 40px;
  color: #fff;
  background: #2f3640;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .4s;
  display: flex;
}

.search-form:hover > .searchInput, .searchInput:focus, .search-form > .searchInput:not(:placeholder-shown) {
  width: 240px;
  padding: 0 6px;
}

.search-form:hover > .searchButton {
  color: #2f3640;
  background: #fff;
}

.load-more {
  height: 50px;
  color: #fff;
  background: #2f3640;
  border-radius: 40px;
  margin: 240px auto 100px;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
  transition: all .4s;
  display: block;
}

.load-more:hover {
  color: #2f3640;
  background: #fff;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 130px;
  display: flex;
}

.photo-card {
  transition: transform var(--animation-duration) var(--timing-function);
  background-color: rgba(255, 226, 187, .835);
  border-radius: 6px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card img {
  height: 360px;
  object-fit: cover;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.photo-card:hover {
  transform: scale(1.03);
}

.info {
  justify-content: center;
  align-items: center;
  gap: 60px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  display: flex;
}

.loader {
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  font-size: 10px;
  animation: mulShdSpin 1.1s infinite;
  position: relative;
  transform: translateZ(0);
}

